.footer {
    background-color: $mainColor;
    padding: 52px 0px 27px 0px;
    @include r(800) {
        padding: 52px 25px 27px 25px; }
    &__video {
        margin-bottom: 124px;
        @include r(900) {
            margin-bottom: 50px; }
        iframe {
            width: 715px;
            height: 402px;
            @include r(900) {
                width: 100%; }
            @include r(700) {
                height: 350px; }
            @include r(480) {
                height: 230px; } } }
    &__logo {
        text-align: center; } }
